.snacks {
    border-top: 4px solid #a5a5a5;
    margin-bottom: 1%;
    # border: 1px solid black;
    # outline-style: solid;
}

p {
    font-size: 1.5em;
    font-family:'Helvetica', 'Arial', 'Sans-Serif';
}

ol li {
    list-style: none;
    margin-left: 3%;
    font-size: 1.2em;
    line-height: 1.4em;
}

ol li:before{
    content: counter(list) ") ";
    counter-increment: list;
}

.hvrbox,
.hvrbox * {
    box-sizing: border-box;
}

.hvrbox {
    position: relative;
    display: inline-block;
    max-width: 20%;
    height: auto;
    margin-bottom : 1%;
    margin-left:2.5%;
    border: 0.1px solid black;
    outline-style: solid;
}

.hvrbox img {
    max-width:100%;

}

.hvrbox .hvrbox-layer_bottom {
    display: block;
}

.hvrbox-layer_top {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(20, 20, 20);
    transition: all 0.4s ease-in-out 0s;
}

.hvrbox:hover .hvrbox-layer_top,
.hvrbox.active .hvrbox-layer_top {
    opacity: 0.7;
}

.hvrbox .hvrbox-text {
    text-align: center;
    font-size: 12px;
    # display: inline-block;
    position: absolute;
    top: 40%;
    left: 50%;
    color: white;
    transform: translate(-50%, -50%);
	margin:1%;
	width:90%;
}

