.Container {
    margin: auto;
    width: 70%;
}

#name a {
    color:black;
    margin-right: 10px;
    font-size: 1.5em;
    font-family:'Helvetica', 'Arial', 'Sans-Serif';
}

nav {
    border-bottom: 1px solid #d5d5d5;
}

ul{
    padding: 0px;
}

ul li {
    display: inline;
    margin-right: 20px;
    font-family:'Helvetica', 'Arial', 'Sans-Serif';
    font-weight: bold;
    padding: 0px;
}

a {
    text-decoration: none;
    color: #999;
    font-family:'Helvetica', 'Arial', 'Sans-Serif';
}

a:hover {
    text-decoration: underline;
}

p {
    font-size: 1.5em;
    font-family:'Helvetica', 'Arial', 'Sans-Serif';
}

.footer {
    position: absolute;
    bottom:15px;
    right:10px;
    left:10px;
}

.footer  {  
    text-align: center;
    font-size: 1em;
}
